import React from 'react';
import PropTypes from 'prop-types';

class NewTodo extends React.Component {
  state = {
    user: this.props.users,
    select: 1,
  }

  sortBySelectedLength = (event) => {
    const { value } = event.target;

    this.setState({
      select: value,
    });
  }

  render() {
    const todo = this.state.user;

    return (
      <div className="todoForm">
        <h1>Add todo form</h1>
        <form>
          <label className="todoForm__element">
            <span>Title : </span>
            <input type="text" placeholder="Add todo title" />
          </label>
          <label className="todoForm__element">
            <span>Select executant : </span>
            <select
              className="button select"
              value={this.state.select}
              onChange={this.sortBySelectedLength}
            >
              {todo.map(item => (
                <option value={item.name} key={item.id}>{item.name}</option>
              ))}
            </select>
          </label>
          <button
            type="submit"
            // onClick={}
            className="todoForm__button todoForm__btn-submit"
          >
            Add Todo
          </button>
          <button
            type="button"
            // onClick={}
            className="todoForm__button todoForm__btn-reset"
          >
            Reset
          </button>
        </form>
      </div>
    );
  }
}

NewTodo.propTypes = {
  users: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.number.isRequired,
    name: PropTypes.string.isRequired,
  })).isRequired,
};

export default NewTodo;
